<template>
    <div>
        <div class="container width90">
        <p class="newstitle">{{this.msg}}</p>
        <div style="font-size:11pt">430100</div>
    </div>
    </div>
</template>
<script>
export default {
    data(){
       return{
        msg:''
       }
    },
    mounted() {
        axios({
            url:'/data/activitys.json'
        }).then(
            res=>{
                console.log(this)
                res.data.forEach(item => {
                    if(item.id == this.$route.params.id){
                       this.msg = item.msg
                    }
                })
            }
        )
        this.$root.title = ''
    },
}
</script>